.image-scroller {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.image-scroller h1 {
  font-size: 2em;
  margin-bottom: 20px;
}

.image-scroller label {
  font-size: 1.2em;
  margin-bottom: 10px;
}

.image-scroller select {
  font-size: 1.2em;
  margin-bottom: 20px;
}

.images {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.image-container {
  position: relative;
  width: 100%;
  max-width: 600px; /* Set the maximum width for images */
  margin-bottom: 20px;
}

.image-container img {
  width: 100%;
  display: block;
}

.image-container button {
  position: absolute;
  bottom: 10px;
  right: 10px;
  padding: 10px 20px;
  font-size: 1em;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  border-radius: 5px;
}